<template>
    <nav aria-label="Page navigation example">
        <ul class="pagination">
            
            <li class="page-item" :class="{disabled: isDeActiveBa}">
                <a class="page-link" href="#" aria-label="Previous"
                    @click="movePageBack">
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                </a>
            </li>      

            <li  class="page-item" 
                v-for="i in pages" 
                :key="i"
                @click.prevent="renderPage(i)">
                <a class="page-link" href="#">{{i}}</a>
            </li>
            
            <li class="page-item"
                :class="{disabled: isDeActiveFo}">

                <a class="page-link" href="#" aria-label="Next" 
                    @click.prevent="movePageFor">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
    created(){
    },
    data(){
        return{
            pageNum : 1,
        }
    },

    props:['totalResult'],
    computed:{
        pages(){
            return Math.ceil(this.totalResult/9);
        },
        isDeActiveFo(){
            return this.pageNum === this.pages;
        },
        isDeActiveBa(){
            return this.pageNum === 1;
        }
    },
    methods:{
        renderPage(i){
            this.pageNum = i;
            this.$emit('renderPage', i);
        },
        movePageFor(){
            if(this.pageNum === this.pages){                
                this.isDeActiveFo = true;

            } else {
                this.pageNum++
                this.$emit('renderPage', this.pageNum);
            }
        },
        movePageBack(){
            if(this.pageNum === 1){                
                this.isDeActiveBa = true;

            } else {
                this.pageNum--
                this.$emit('renderPage', this.pageNum);

            }
        },
        
    }

}
</script>

